
import router from '../router';
import firebaseApp from '../firebase';
const AUTH = firebaseApp.auth();

let actions = {

    sigup( { commit }, creds) {

        AUTH.createUserWithEmailAndPassword(creds.email, creds.password)
            .then( user =>{

                //SEND EMAIL VERIFICATION
                user.sendEmailVerification().then(function() {

                    console.log('email sent');

                }, function(error) {

                    console.log(error);

                });


            })
            .catch( e => {

                console.log(e.message);

            })

    },


    login( { commit }, creds) {

        AUTH.signInWithEmailAndPassword(creds.email, creds.password)

            .then( user => {

                // CHECK IF USER IS VERIFIED
                if(user.emailVerified !== false){

                    commit('LOGGING');

                    //lets fake a timeout
                    setTimeout(function () {

                        router.push('/'); //redirect to main view

                        commit('LOGIN');

                    }, 1000)

                }else{

                    console.log("USER NOT VERIFIED: CHECK EMAIL");

                }


            })
            .catch( e => {

                //console.log(e);

                commit('VALIDATION');

            });

    },


    logout( { commit } ){

        AUTH.signOut();

        localStorage.removeItem('TOKEN');

        window.location.href= "/amerisleep-cms/login"; //security improvement: force app refresh/redirect to login

        commit('LOGOUT');

    },

    //
    // add_new_size( {commit} ){
    //
    //     Vue.http.get('as1/sizes.json').then( res => {
    //
    //         commit('ADD_NEW_SIZE', res.body)
    //
    //     });
    //
    // }

};

export default actions;


